/* General reset and box-sizing definition */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Apply some basic styling for the whole page */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 20px;
    background: white;
    line-height: 1.6;
}

/* Style the container for the first section */
.container {
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    margin: 40px auto;
    /* Adjusted for consistency */
    margin-right: 70px;
    margin-left: 70px;
    margin-bottom: 60px;

}

/* Style the content section for the first section */
.textSection {
    width: 55%;
}

/* ... (other styles for the first section) */

/* Style for the second section */
.donorSection {
    max-width: 1200px;
    margin: 40px auto;
    padding: 20px 10px;
    margin-top: 60px;
}

.donorSectionHeader {
    text-align: center;
    font-size: 36px;
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
}

.donorSectionContent {
    display: flex;
    justify-content: space-between;
    text-align: left;
}

.donorColumn {
    flex-basis: 30%;
}

.donorColumn h3 {
    font-size: 20px;
    color: #333;
    margin-bottom: 10px;
}

.donorColumn p {
    font-size: 15px;
    color: #666;
    margin-right: 5px;
    margin-left: 5px;
}

.section {
    margin-bottom: 60px;
    /* Space between sections */
}

.donationList {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    font-size: 18px;
}

.donationList li::before {
    content: '✓';
    color: #a72828;
    margin-right: 10px;
    font-weight: bold;
    font-size: 24px;
}

.statisticsSection {
    max-width: 1200px;
    margin: 60px auto;
    /* Adds more space above and below the section */
    padding: 20px;
    text-align: center;
    /* Center-aligns the text for all statistic cards */
}

.statisticsContent {
    display: flex;
    justify-content: space-around;
    /* Evenly spaces the statistic cards */
    flex-wrap: wrap;
    /* Allows the cards to wrap on smaller screens */
    gap: 20px;
    /* Adds space between the cards */
}

.statisticCard {
    flex: 1;
    /* Allows the cards to grow */
    min-width: 250px;
    /* Minimum width for smaller screens */
    background: #f7f7f7;
    /* Light background for the cards */
    border-radius: 15px;
    /* Rounded corners for the cards */
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* Subtle shadow for depth */
}

.statisticCard h3 {
    margin: 0;
    color: #333;
    font-size: 22px;
    /* Slightly smaller to fit on the card */
}

.statisticCard .statNumber {
    font-size: 40px;
    /* Large font size for emphasis */
    color: #d9534f;
    /* A strong color for the statistic number */
    font-weight: bold;
    margin: 10px 0;
    /* Space above and below the number */
}

/* Responsive adjustment */
@media (max-width: 768px) {
    .statisticsContent {
        flex-direction: column;
    }

    .statisticCard {
        width: 100%;
        /* Full width cards on smaller screens */
        margin-bottom: 20px;
    }
}